.App {
  text-align: center;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(90deg, #000000, #434343, #0F2027, #203A43);
	background-size: 400% 400%;
	animation: gradient 5s ease infinite;
  color: #00FFFF;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.heading {
  color: #ee7752;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #191414; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: azure; 
  border-radius: 70px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: aqua; 
}